import React from "react"
import { graphql, Link } from "gatsby"
import { css } from "@emotion/core"
import Helmet from "react-helmet"
import Layout from "../component/layout"
import ISSO from "../component/comment"

const headerStyle = css`
  margin: 1rem 0 0.5rem 0;
  font-size: clamp(1.1rem, 1vw + 1.0rem, 1.4rem);
`

const authorStyle = css`
  display: flex;
  font-size: clamp(0.6rem, 1vw + 0.6rem, 0.8rem);
  color: grey;
  margin-bottom: 1rem;
  p:first-of-type {
    margin-right: 1rem;
  }
  a {
    color: grey;
    :hover {
      text-decoration: none;
    }
  }
`

const markdownStyle = css`
  line-height: 1.5;
  margin-bottom: 5rem;
  font-size: clamp(0.7rem, 1vw + 0.7rem, 1.0rem);
  h2 {
    margin-top: 2rem;
    font-size: clamp(1.0rem, 1vw + 1.0rem, 1.3rem);
    ::before {
      content: '# '
    }
  }
  h3 {
    margin-top: 2.5rem;
    font-size: clamp(0.9rem, 1vw + 0.9rem, 1.2rem);
    ::before {
      content: '## '
    }
  }
  p {
    text-align: justify;
  }
  .task-list-item {
    list-style-type: none;
  }
  ul {
    padding-left: 0.5rem;
    list-style-position: inside;
    li {
      margin: 0.3rem 0;
    }
  }
  table {
    margin: 2rem auto;
    border-collapse: collapse;
    line-height: 1.8;
  }
  table td,
  table th {
    border: 1px solid black;
    padding: 0 0.4rem;
  }
  table th {
    background: #ddd;
  }
  blockquote {
    margin: 0;
    border-left: 2px solid grey;
  }
  blockquote p {
    margin-left: 1rem;
  }
  a {
    color: black;
    text-decoration: none;
    display: inline-block;
    position: relative;
  }
  a:after {
    content: '';
    display: block;
    position: absolute;
    bottom: 2px;
    background-color: rgba(183, 28, 28, 0.25);
    width: 100%;
    height: 0.5rem;
  }
  a:hover:after {
    text-decoration: none;
    background-color: rgba(242, 156, 56, 0.25);
  }
`

export default ({ data }) => {
  const node = data.allMarkdownRemark.edges[0].node
  const html = node.html
  const title = node.frontmatter.title
  const postDate = node.frontmatter.date
  const slug = node.frontmatter.slug
  const description = node.frontmatter.description
  return (
    <Layout>
      <Helmet>
        <title>{title}</title>
        <meta name="description" content={description} />
      </Helmet>
      <article>
        <header>
          <h1 css={headerStyle}>{title}</h1>
          <address css={authorStyle}>
            <p>
              作者: <Link to="/about/">科技微讯</Link>
            </p>
            <p>日期: <time dateTime={postDate}>{postDate}</time></p>
          </address>
        </header>
        <section css={markdownStyle} dangerouslySetInnerHTML={{ __html: html }} />
      </article>
      <ISSO title={title} slug={`blog/${slug}/`} />
    </Layout>
  )
}

export const query = graphql`
  query($slug: String!) {
    allMarkdownRemark(filter: { frontmatter: { slug: { eq: $slug } } }) {
      edges {
        node {
          html
          frontmatter {
            title
            date(formatString: "YYYY/MM/DD")
            slug
            description
          }
        }
      }
    }
  }
`
