import React, { useEffect } from "react"

const ISSO = ({ title, slug }) => {
  useEffect(() => {
    const doc = window.document
    const issoSection = doc.getElementById("isso-thread")
    var script = doc.createElement("script")
    script.dataset.isso = "//comment.kejiweixun.com/"
    script.src = "//comment.kejiweixun.com/js/embed.min.js"
    // script.dataset.isso = '//comment.localhost/';
    // script.src = '//comment.localhost/js/embed.min.js';
    issoSection.parentNode.insertBefore(script, issoSection.nextSibling)
  }, [])
  return <section id={`isso-thread`} data-title={title} data-isso-id={slug} />
}

export default ISSO
